es inner padding and border in FF3+\n// www.sitepen.com/blog/2008/05/14/the-devils-in-the-details-fixing-dojos-toolbar-buttons/\n\nbutton, input {\n  &::-moz-focus-inner {\n    border: 0;\n    padding: 0;\n  }\n}\n\n// 1. Removes default vertical scrollbar in IE6/7/8/9\n// 2. Improves readability and alignment in all browsers\n\ntextarea {\n  overflow: auto; // 1\n  vertical-align: top; // 2\n}\n\n// =============================================================================\n// Tables\n// =============================================================================\n\n// Remove most spacing between table cells\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\ninput {\n  // 1. Addresses appearance set to searchfield in S5, Chrome\n  // 2. Addresses box-sizing set to border-box in S5, Chrome (include -moz to future-proof)\n  &[type=\"search\"] {\n    -webkit-appearance: textfield; // 1\n    -moz-box-sizing: content-box;\n    -webkit-box-sizing: content-box; // 2\n    box-sizing: content-box;\n\n    // Remove inner padding and search cancel button in Safari 5 and Chrome\n    // on OS X.\n    &::-webkit-search-cancel-button,\n    &::-webkit-search-decoration {\n      -webkit-appearance: none;\n    }\n  }\n\n  // 1. Address box sizing set to `content-box` in IE 8/9/10.\n  // 2. Remove excess padding in IE 8/9/10.\n  // 3. Removes excess padding in IE7\n  //    Known issue: excess padding remains in IE6\n  &[type=\"checkbox\"],\n  &[type=\"radio\"] {\n    box-sizing: border-box; // 1\n    padding: 0; // 2\n    @if $legacy_support_for_ie {\n      *height: 13px; // 3\n      *width: 13px; // 3\n    }\n  }\n}\n*{\n  box-sizing: border-box;\n}","%transition {\n  transition: all 0.8s ease-out;\n}\n\n%font-prim {\n  font-family: 'Georgia', Verdana, sans-serif;\n}\n\n%font-add {\n  font-family: 'Arial', Verdana, sans-serif;\n}","@font-face {\n  font-family: 'Georgia';\n  src: url('../../fonts/Georgia.woff2') format('woff2'),\n  url('../../fonts/Georgia.woff') format('woff'),\n  url('../../fonts/Georgia.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'Arial';\n  src: url('../../fonts/ArialMT.woff2') format('woff2'),\n  url('../../fonts/ArialMT.woff') format('woff'),\n  url('../../fonts/ArialMT.ttf') format('truetype');\n  font-weight: normal;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'Georgia';\n  src: url('../../fonts/Georgia-Bold.woff2') format('woff2'),\n  url('../../fonts/Georgia-Bold.woff') format('woff'),\n  url('../../fonts/Georgia-Bold.ttf') format('truetype');\n  font-weight: bold;\n  font-style: normal;\n  font-display: swap;\n}\n\n@font-face {\n  font-family: 'Arial';\n  src: url('../../fonts/Arial-ItalicMT.woff2') format('woff2'),\n  url('../../fonts/Arial-ItalicMT.woff') format('woff'),\n  url('../../fonts/Arial-ItalicMT.ttf') format('truetype');\n  font-weight: normal;\n  font-style: italic;\n  font-display: swap;\n}\n","html{\n  font-size: 10px;\n  scroll-behavior: smooth;\n}\nbody{\n  @extend %font-prim;\n  @include font($size: 1.5rem, $height: 160%, $spacing: 0);\n  color: $color-dark-xl;\n  width: 100%;\n  background: $color-light-xl;\n  cursor: default;\n  overflow-x: hidden;\n  a:checked &{\n    opacity: 0%;\n    transition: opacity 5s ease-in 0.15s;\n  }\n}\n.scroll-hidden{\n  overflow: hidden;\n}\na{\n  @extend %transition;\n}\nh1{\n  @include font($size: 4.4rem, $height: 130%, $spacing: 0);\n  margin: 0;\n  @include media-mobile{\n    @include font($size: 2.5rem, $height: 130%, $spacing: 0.06em);\n  }\n}\n\nh3{\n  @include font($size: 3.5rem, $height: 130%);\n  margin: 0;\n  @include media-mobile{\n    @include font($size: 2.5rem, $height: 130%, $spacing: 0.06em);\n  }\n}\nh4{\n  @include font($size: 2.0rem, $height: 115%);\n  margin: 0;\n  @include media-mobile{\n    @include font($size: 1.5rem, $height: 110%, $spacing: 0.06em);\n  }\n}\n\nh5{\n  @include font($size: 15px, $height: 17px, $spacing: 0.06em);\n  margin: 0;\n  @include media-laptop{\n    @include font($size: 1.3rem, $height: 110%, $spacing: 0.06em);\n  }\n\n}","@mixin font ($size, $height: $size, $weight: normal, $style: normal, $spacing: 0.06em) {\n  font-size: $size;\n  font-style: $style;\n  font-weight: $weight;\n  line-height: $height;\n  letter-spacing: $spacing;\n}\n\n@mixin media-content {\n  @media (max-width: $content-width) { @content; };\n}\n@mixin media-laptop-big {\n  @media (max-width: $laptop-big-width) { @content; };\n}\n@mixin media-change-carousel {\n  @media (max-width: $change-carousel-width) { @content; };\n}\n@mixin media-change-help-list {\n  @media (max-width: $change-help-list-width) { @content; };\n}\n@mixin media-laptop {\n  @media (max-width: $laptop-width) { @content; };\n}\n@mixin media-laptop-small {\n  @media (max-width: $laptop-small-width) { @content; };\n}\n@mixin media-tablet-big {\n  @media (max-width: $tablet-big-width) { @content; };\n}\n@mixin media-tablet {\n  @media (max-width: $tablet-width) { @content; };\n}\n@mixin media-change-menu {\n  @media (max-width: $change-menu-type-width) { @content; };\n}\n@mixin media-tablet-small {\n  @media (max-width: $tablet-small-width) { @content; };\n}\n@mixin media-change-position-help-list {\n  @media (max-width: $change-position-help-list-width) { @content; };\n}\n@mixin media-between-tablet-mobile {\n  @media (max-width: $between-tablet-mobile-width) { @content; };\n}\n@mixin media-mobile {\n  @media (max-width: $mobile-big-width) { @content; };\n}\n@mixin media-mobile-small {\n  @media (max-width: $mobile-small-width) { @content;};\n}","$color-dark-m: #B2B2B2;\n$color-dark-l: #545454;\n$color-dark-xl: #4C4C4C;\n$color-dark-3xl: #292929;\n$color-dark-4xl: #000000;\n$color-primary: #F1CDB3;\n$color-primary-light: #FDDCC4;\n$color-dark-s: #CDCDCD;\n$color-light-s: #FAFAFA;\n$color-light-l: #F6F6F6;\n$color-light-xl: #FFFFFF;\n$background-gradient-start: #513D2F;\n$background-header-gradient-stop: #211F20;\n$background-footer-gradient-stop: #1A1A1C;\n\n\n$content-width: 1280px;\n$laptop-big-width: 1180px;\n$change-carousel-width: 1125px;\n$change-help-list-width: 1101px;\n$laptop-width: 1070px;\n$laptop-small-width: 960px;\n$tablet-big-width: 780px;\n$tablet-width: 768px;\n$change-menu-type-width: 767.8px;\n$tablet-small-width: 760px;\n$change-position-help-list-width: 670px;\n$between-tablet-mobile-width: 480px;\n$mobile-big-width: 414px;\n$mobile-small-width: 320px;\n",".wrapper{\n  box-sizing: border-box;\n  max-width: 1280px;\n  padding: 0 40px;\n  margin: 0 auto;\n  @include media-content{\n    padding: 0 calc(30px + (40 - 30) * ((100vw - 768px)/(1280 - 768)));\n  }\n  @include media-tablet-small{\n    padding: 0 calc(10px + (30 - 10) * ((100vw - 320px)/(760 - 320)));\n  }\n}",".header{\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  .wrapper{\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: center;\n    padding-top: 30px;\n    padding-bottom: 30px;\n    @include media-tablet-small {\n      padding: 30px calc(20px + (30 - 20) * ((100vw - 320px)/(760 - 320)));\n    }\n  }\n}\n\n.header__hamburger{\n  display: none;\n  @include media-change-menu{\n    display: inline-block;\n  }\n}\n",".footer{\n  background: #211F20;\n  background: url(\"../../img/background.png\"), linear-gradient(-60deg, $background-footer-gradient-stop,#211F20, $background-gradient-start 80%) ;\n  .wrapper{\n    padding-top: 40px;\n    display: flex;\n    flex-direction: row;\n    gap: 160px;\n    @include media-content{\n      flex-wrap: wrap;\n      justify-content: space-between;\n      gap: calc(80px + (160 - 80) * ((100vw - 1180px)/(1280 - 1180)));\n      padding-top: calc(30px + (40 - 30) * ((100vw - 768px)/(1280 - 768)));\n      @include media-laptop{\n        justify-content: center;\n        row-gap: 65px;\n        column-gap: 60px;\n        @include media-between-tablet-mobile{\n          row-gap: 40px;\